<script>
import axios from "axios";

export default {
  name: "studentStat",
  props: ["selectedStudentName"], // props

  data() {
    return {
      studentInfo: {
        name: "",
        halfPreviousYear: "",
        firstClass: "",
        scndClass: "",
        thirdClass: "",
        lastClass: "",
        div: "",
        affi: "",
      },
    };
  },

  watch: {
    selectedStudentName() {
      this.getStudentData(); // Get information when selectedStudentName changes.
    },
  },

  created() {
    this.getStudentData(); // Get student information when component is created.
  },

  methods: {
    async getStudentData() {
      try {
        const response = await axios.get(
          `https://raw.githubusercontent.com/Pwang-je/ScoreBoard/main/stuStat.json?name=${this.selectedStudentName}`
        );

        console.log("selectedStudentName : ", this.selectedStudentName);
        // find the student by name in the response data;
        const selectedStudent = response.data.find(
          (student) => student.name === this.selectedStudentName
        );

        if (selectedStudent) {
          this.studentInfo = selectedStudent;
        } else {
          console.error('student "${this.selectedStudentName}" not found.');
        }

        // this.studentInfo = response.data[0];
        // console.log("stustat-받아온 이름 ", this.selectedStudentName);
      } catch (error) {
        console.error("Error: ", error);
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

<template>
  <template v-if="studentInfo">
    <div class="container-fluid">
      <!-- <div class="col" style="inline-height: 2.2rem;"> -->

      <div class="row row-cols-6 neu-stuflat size-flat">
        <div class="col font-small mb-pd-size">
          <div class="neu-stuflat text-center size-upper">겨울학기</div>
        </div>

        <div class="col font-small mb-pd-size">
          <div class="neu-stuflat text-center size-upper">1학기</div>
        </div>

        <div class="col font-small mb-pd-size">
          <div class="neu-stuflat text-center size-upper">여름학기</div>
        </div>

        <div class="col font-small mb-pd-size">
          <div class="neu-stuflat text-center size-upper">2학기</div>
        </div>

        <div class="col font-small mb-pd-size">
          <div class="neu-stuflat text-center size-upper">구분</div>
        </div>

        <div class="col font-small mb-pd-size">
          <div class="neu-stuflat text-center size-upper">계열</div>
        </div>

        <div class="col text-center font-large mb-font-size">
          {{ studentInfo.firstClass }}
        </div>

        <div class="col text-center font-large mb-font-size">
          {{ studentInfo.scndClass }}
        </div>

        <div class="col text-center font-large mb-font-size">
          {{ studentInfo.thirdClass }}
        </div>

        <div class="col text-center font-large mb-font-size">
          {{ studentInfo.lastClass }}
        </div>

        <div class="col text-center font-large mb-font-size">
          {{ studentInfo.div }}
        </div>

        <div class="col text-center font-large mb-font-size">
          {{ studentInfo.affi }}
        </div>
      </div>
    </div>
  </template>

  <!-- </div>
  </div> -->
</template>

<style>
.t-margin {
  margin: 0 1rem;
}
.font-small {
  padding-top: 0.4em;
  margin-bottom: 0.6rem;
  font-size: clamp(0.6rem, 1.5vw, 0.9rem);
}
.font-large {
  font-size: clamp(1rem, 1.5vw, 1rem);
}
.size-flat {
  padding: 0.6rem 0.6rem;
}
.size-upper {
  padding: 0.4rem 0 0.4rem 0;
  font-size: 1rem;
}
.neu-stuflat {
  background: #f0f0f3;
  border-radius: 12px;
  box-shadow: -7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9,
    7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001, inset 0px 0px 0px 0px #fff9,
    inset 0px 0px 0px 0px #0001, inset 0px 0px 0px 0px #fff9,
    inset 0px 0px 0px 0px #0001;
}
.neu-stupressed {
  background: #f0f0f3;
  border-radius: 12px;
  box-shadow: inset -7px -7px 20px 0px #fff9, inset -4px -4px 5px 0px #fff9,
    inset 7px 7px 20px 0px #0002, inset 4px 4px 5px 0px #0001,
    0px 0px 0px 0px #fff9, 0px 0px 0px 0px #0001, 0px 0px 0px 0px #fff9,
    0px 0px 0px 0px #0001;
}

@media (max-width: 768px) {
  .mb-font-size {
    font-size: 0.8rem;
  }
  .mb-pd-size {
    padding: 0.4rem !important;
  }
}
</style>
