<template>
    <div>        
        <div class="chart-size">
            <Bar
                v-if="loaded"
                :data="chartData"
                :options="CmpChartOptions"
                class="chart-padding flx-heg"
            />
        </div>
    </div>
</template>

<script>
import axios from "axios";
import { Bar } from "vue-chartjs";
import { Chart, registerables } from "chart.js";
Chart.register(...registerables);

export default {
    name: "vtcChart",
    components: { Bar },
    props: {
        selectedStudentName: {
            type: String,
            default: "",
            required: true,
        }
    },

    data: () => ({

        loaded: false,
        chartData: {
            labels: [],
            datasets: [
                // Initial dataset structure
                {
                    label: 'Eng',
                    data: [],
                    backgroundColor: "rgba(54, 162, 235, 0.3)",
                    borderColor: "rgb(54, 162, 235)",
                    borderWidth: 2,
                    borderRadius: 10,
                    borderSkipped: false,
                },
                {
                    label: 'Math',
                    data: [],
                    backgroundColor: "rgba(255, 99, 132, 0.3)",
                    borderColor: "rgb(255, 99, 132)",
                    borderWidth: 2,
                    borderRadius: 10,
                    borderSkipped: false,
                },
            ],
        },
        CmpChartOptions: {
            responsive: true,
            maintainAspectRatio: false,
            devicePixelRatio: 2,
            plugins: {
                legend: {
                    position: 'top',
                },
                title: {
                    display: true,
                    text: '월별 평균 대비 성적'
                }
            },
        },
        cmpScore: null,

    }),

    created() {
        this.getCmpData();
    },

    watch: {
        selectedStudentName(newSelectedStudentName) {
            console.log("cmpBar.vue selected student name : ", newSelectedStudentName);
            this.loaded = false;
            this.getCmpData();  // get information when selected student name changes.
        }
    },

    methods: {

        getCmpData() {
            axios
                .get("https://raw.githubusercontent.com/Pwang-je/ScoreBoard/main/cmpred.json?name=${this.selectedStudentName}")
                .then((res) => {
                    console.log("cmpbar.vue selected student name is : ", this.selectedStudentName);
                    const selectedStudent = res.data.find(student => student.name === this.selectedStudentName);

                    if(selectedStudent) {
                        const {febeng, febmath, mareng, marmath, apreng, aprmath, mayeng, maymath, 
                            juneng, junmath, juleng, julmath, augeng, augmath, sepeng, sepmath } = selectedStudent;

                        this.updateCmpChartData(febeng, febmath, mareng, marmath, apreng, aprmath, mayeng, maymath, 
                            juneng, junmath, juleng, julmath, augeng, augmath, sepeng, sepmath);
                    } else {
                        console.log("student name dat not found for ", this.selectedStudentName);
                    }
                    this.loaded = true;     // set loaded to true when data is loaded.
                })
                .catch((error) => {
                    console.log("fuck cmpdata error : ", error);
                    this.loaded = true;     // Ensure loaded is set even on error.
                })
        },

        updateCmpChartData(febeng, febmath, mareng, marmath, apreng, aprmath, mayeng, maymath, 
                            juneng, junmath, juleng, julmath, augeng, augmath, sepeng, sepmath) {
            this.chartData = {
                labels: ['2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월'],
                datasets: [
                    {
                        label: 'Eng',
                        data: [ febeng, mareng, apreng, mayeng, juneng, juleng, augeng, sepeng ],
                        backgroundColor: "rgba(54, 162, 235, 0.3)",
                        borderColor: "rgb(54, 162, 235)",
                        borderWidth: 2,
                        borderRadius: 10,
                        borderSkipped: false,
                    },
                    {
                        label: 'Math',
                        data: [ febmath, marmath, aprmath, maymath, junmath, julmath, augmath, sepmath],
                        backgroundColor: "rgba(255, 99, 132, 0.3)",
                        borderColor: "rgb(255, 99, 132)",
                        borderWidth: 2,
                        borderRadius: 10,
                        borderSkipped: false,
                    }
                ]
            }
        }


    },


}

</script>
<style>
.chart-size {
    height: 20rem;
    margin-bottom: 2rem;
}
.chart-padding {
    padding: 1rem;
}
@media (max-width: 768px) {

}
</style>
